import { useEffect, useState } from 'react';
//DATA
import commonImgData from '../data/CommonAssets.json';
//HOOKS
import usePlayerStoreData from './usePlayerStoreData';
//IMAGES
import UndeadommonImg from '../img/undead/UndeadCommon.png';
import DemonommonImg from '../img/demon/DemonCommon.png';
import DrowommonImg from '../img/drow/DrowCommon.png';
import HumanommonImg from '../img/human/HumanCommon.png';
import ElfommonImg from '../img/elf/ElfCommon.png';

export default function useRaceCommonImg( player, index ) {
  const playerData = usePlayerStoreData( player );
  const [ img, setImg ] = useState( `url(${ UndeadommonImg }) ${ commonImgData[ index ] }` );
  const { race } = playerData;

  //USE EFFECT
  useEffect(() => {
    switch ( race ) {
      case 'undead':
        setImg( `url(${ UndeadommonImg }) ${ commonImgData.undead[ index ] }` );
      break;
      case 'demon':
        setImg( `url(${ DemonommonImg }) ${ commonImgData.demon[ index ] }` );                
      break;
      case 'drow':
        setImg( `url(${ DrowommonImg }) ${ commonImgData.drow[ index ] }` );                
      break;
      case 'human':
        setImg( `url(${ HumanommonImg }) ${ commonImgData.human[ index ] }` );
      break;
      case 'elf':
        setImg( `url(${ ElfommonImg }) ${ commonImgData.elf[ index ] }` );
      break;
      default:
      break;
  }
  }, [ race, index ]);

  return img;
}