//HOOKS
import useBattlefield from "./hooks/useBattlefield.js";
//STYLES
import { SelectorBox, SelectorLabel, Select } from "./styles/BattlefieldSelector.styled";

export default function BattlefieldSelector() {
  const setBattlefield = useBattlefield();

  return (
    <SelectorBox>
      <SelectorLabel> </SelectorLabel>
      <Select id = "battlefield" onChange = { setBattlefield } >
        <option value = "cursedForest" > </option>
        <option value = "deadLand" > </option>
        <option value = "hollyLand" > </option>
        <option value = "magicForest" > </option>
        <option value = "mountain" ></option>
        <option value = "desert" ></option>
        <option value = "forest" ></option> 
        <option value = "steppe" ></option>
        <option value = "mine" ></option>
      </Select>  
    </SelectorBox>
 
  )
}